let animatedElements = document.querySelectorAll('.animated');

let Visible = function(target) {

    // Все позиции элемента
    let targetPosition = {
            top: window.pageYOffset + target.getBoundingClientRect().top,
            left: window.pageXOffset + target.getBoundingClientRect().left,
            right: window.pageXOffset + target.getBoundingClientRect().right,
            bottom: window.pageYOffset + target.getBoundingClientRect().bottom
        },
        // Получаем позиции окна
        windowPosition = {
            top: window.pageYOffset,
            left: window.pageXOffset,
            right: window.pageXOffset + document.documentElement.clientWidth,
            bottom: window.pageYOffset + document.documentElement.clientHeight
        };

    if (targetPosition.bottom - 40 > windowPosition.top &&
        targetPosition.top + 40 < windowPosition.bottom) {
        target.classList.add("animated--active")

        if(target.classList.contains("m-video")) {
            target.addEventListener('click',function(){
                target.play();
            },false);
            target.click();
            target.play();
        }
    } else {

    }
};

$(".m-video").click(function (e) {
    e.stopPropagation()
    e.preventDefault()
})

// Запускаем функцию при прокрутке страницы
window.addEventListener('scroll', function() {
    // Visible (element);
    initAnimElements()
});
initAnimElements()
// А также запустим функцию сразу. А то вдруг, элемент изначально видно
// Visible (element);

function initAnimElements() {
    animatedElements.forEach(el => {
        if(el.classList.contains("_svg")){
            let paths = el.querySelectorAll(`path`);
            paths.forEach(path => {
                let pathLength = path.getTotalLength();
                path.style.strokeDasharray = pathLength + ' ' + pathLength;
                path.style.strokeDashoffset = pathLength * -1;
            })
        }
        Visible(el)
    })
}
