$("._toggle-header-menu").click(function () {
    $(".m-page").toggleClass("_open-menu")
    $(".m-page").toggleClass("fixed-body")
})

$(".header-menu .menu-item-has-children>a").click(function () {
    $(this).parents("li").toggleClass("_active")
    $(this).siblings("ul").slideToggle()
})

$("._search-field").on("input",function () {
    if($(this).val().length > 0){
        $(".search-modal").addClass("_show-result")
    } else{
        $(".search-modal").removeClass("_show-result")
    }
})

$(".search-modal__clear,.search-modal__bg").click(function () {
    closeSearch()
})

function closeSearch(){
    $('.search-modal ._search-field').val("").trigger("input");
    $('.search-modal').removeClass("_open")
    $("body").removeClass("fixed-body")
}

$(".header-search").click(function () {
    $('.search-modal ._search-field').focus()
    $('.search-modal').addClass("_open")
    $("body").addClass("fixed-body")
})

window.addEventListener('resize', () => {
    setHeaderVisible()
})
setHeaderVisible()



function setHeaderVisible() {
    let header = document.querySelector(".header")
    if(!header)
        return
    window.addEventListener('scroll', function(){
        let scrolled = window.pageYOffset || document.documentElement.scrollTop;

        if ( scrolled > 60 ){
            header.classList.add('_fix');
        } else {
            header.classList.remove('_fix');
        }
    });

}
