setSDEKTypeFields()
setSDEKTypeFields2()
$("input[name='sdekType']").change(function () {
    setSDEKTypeFields()
})
$("input[name='sdekType2']").change(function () {
    setSDEKTypeFields2()
})

$("input[data-name='for-other']").change(function () {
    let isChecked = $(this).prop("checked")
    console.log(isChecked)
    if(isChecked){
        $(".order-other__bottom").slideDown()
    }else{
        $(".order-other__bottom").slideUp()
    }
})

function setSDEKTypeFields() {
    let checkedField = $("input[name='sdekType']:checked")
    let checkedVal = $(checkedField).attr("data-value")


    $("._tab1 .order-delivery__content").removeClass("_active")
    $(`._tab1 .order-delivery__content.${checkedVal}`).addClass("_active")
}

function setSDEKTypeFields2() {
    let checkedField = $("input[name='sdekType2']:checked")
    let checkedVal = $(checkedField).attr("data-value")


    $("._tab2 .order-delivery__content").removeClass("_active")
    $(`._tab2 .order-delivery__content.${checkedVal}`).addClass("_active")
}
