$("._toggle-lk-menu").click(function () {
    $("body").toggleClass("_open-lk-menu")
    $("body").toggleClass("fixed-body")
})
$(".order-header__btn._toggle").click(function () {
    $(this).parents(".order-item").toggleClass("_open")
    $(this).parents(".order-item").find(".order-item__content").slideToggle()
})
