$("._tab").click(function() {
    let parentBlock = $(this).parents("._tabs-parent")
    let tabId = $(this).attr("data-tab")
    $(parentBlock).find("._tab").removeClass("_active")
    $(this).addClass("_active")
    $(parentBlock).find(".tab-content").removeClass("_active")
    $(parentBlock).find(`.${tabId}`).addClass("_active")
})

$(".pass-toggle").click(function () {
    if($(this).hasClass("_active")){
        $(this).removeClass("_active")
        $(this).siblings("input").attr("type","password")
    } else{
        $(this).addClass("_active")
        $(this).siblings("input").attr("type","text")
    }
})

$(".favourite-btn,.compare-btn").click(function () {
    $(this).toggleClass("_added")
})

$(".footer-nav__show").click(function () {
    if(window.innerWidth<=576){
        $(this).parents(".footer-nav").toggleClass("_active")
        $(this).siblings(".footer-nav__hidden").slideToggle()
    }
})

setPlaceholders()
function setPlaceholders(){
    $("[data-placeholder2]").each(function (i,el) {
        let placeholder = $(el).attr("data-placeholder")
        let placeholder2 = $(el).attr("data-placeholder2")


        if(window.innerWidth>992){
            $(el).attr("placeholder",placeholder)
        } else{
            $(el).attr("placeholder",placeholder2)
        }
    })
}

$(".scroll-up").click(function() {
    $("body,html").scrollTop(0);
})
$(window).scroll(function() {
    let documentHeight = $(document).height()
    let footerHeight = $(".footer").height()
    let windowHeight = $(window).height()
    console.log(documentHeight)
    console.log(footerHeight)
    console.log($(window).scrollTop())
    if ($(window).scrollTop() > 100) {
        $(".scroll-up").addClass("scroll-up--active")
    } else {
        $(".scroll-up").removeClass("scroll-up--active")
    }

    if ($(window).scrollTop() > (documentHeight-windowHeight-footerHeight)) {
        $(".scroll-up").addClass("_white")
    } else {
        $(".scroll-up").removeClass("_white")
    }
})
