function mainSlider() {
    var swiper = new Swiper('.main-slider .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        navigation: {
            nextEl: '.main-slider .swiper-button-next',
            prevEl: '.main-slider .swiper-button-prev',
        },
        scrollbar: {
            el: `.main-slider .swiper-scrollbar`,
            draggable: true,
            snapOnRelease: true,
            dragSize: 'auto',
        },
        autoplay: {
            delay: 7000,
        },
        on: {
            init: function () {
                setHeaderColor()
            },
        },
    })
    swiper.on('slideChangeTransitionEnd', function () {
        setHeaderColor()
    });

    function setHeaderColor() {
        let isWhite = $(".main-slider .swiper-slide-active").hasClass("_white")
        if(isWhite){
            $(".main-page").addClass("_white-header")
        }else{
            $(".main-page").removeClass("_white-header")
        }
    }
}

function Sliders() {
    let SlidersArray = []

    let sliders = document.querySelectorAll(".slider")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)
        let isLoop = slider.classList.contains("_loop")
        let isAutoplay = slider.classList.contains("_autoplay")

        let newSwiper = new Swiper(`.slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            watchSlidesProgress: true,
            preventClicks :true,
            a11y: false,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
            loop: isLoop,
            autoplay: isAutoplay,
            navigation: {
                nextEl: `.slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.slider[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
            },
            scrollbar: {
                el: `.slider[data-slider-id="${index}"] .swiper-scrollbar`,
                draggable: true,
                snapOnRelease: true,
                dragSize: 'auto',
            },
        })
        if(isAutoplay){
            newSwiper.params.autoplay.disableOnInteraction = false
        }
        SlidersArray[index] = newSwiper
    })
}

function gallery() {
    let SlidersArray = []

    let sliders = document.querySelectorAll(".gallery")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)
        let gallery = {
            mySwiper: {},
            mySwiper2: {}
        }

        gallery.mySwiper = new Swiper(`.gallery[data-slider-id="${index}"] .mySwiper`, {
            spaceBetween: 0,
            slidesPerView: "auto",
            freeMode: true,
            watchSlidesProgress: true,
            direction: "vertical",
            breakpoints: {
                320: {
                    direction: "horizontal",
                },
                992: {
                    direction: "vertical",
                }
            }
        });
        gallery.mySwiper2 = new Swiper(`.gallery[data-slider-id="${index}"] .mySwiper2`, {
            spaceBetween: 0,
            direction: "vertical",
            navigation: {
                nextEl: `.gallery[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.gallery[data-slider-id="${index}"] .swiper-button-prev`,
            },
            thumbs: {
                swiper:  gallery.mySwiper,
            },
            breakpoints: {
                320: {
                    direction: "horizontal",
                },
                992: {
                    direction: "vertical",
                }
            },
            on: {
                init: function () {
                    playPauseVideo()
                },
            },
        });

        gallery.mySwiper2.on('slideChangeTransitionEnd', function () {
            playPauseVideo()
        });

        function playPauseVideo(){
            let activeSlide = document.querySelector(`.gallery[data-slider-id="${index}"] .mySwiper2 .swiper-slide-active`)

            if(activeSlide.classList.contains("gallery__img--video")){
                let video = activeSlide.querySelector("video")
                video.play();
            }
        }
        SlidersArray[index] = gallery
    })

}


Sliders()
gallery()
mainSlider()
